<template>
  <b-container v-if="render" class="relevanteFactorenWrapper">
    <b-row v-if="forExport" :class="{'export-class': forExport, 'html2pdf__page-break': forExport, 'effectRow': true}" >
      <b-alert show>Copyright TNO in opdracht van het Ministerie van Financiën. Het betreft hier een prototype dat gepubliceerd is in september 2022. Dit prototype dient voor test & evaluatie doeleinden: aan de uitkomsten van de huidige versie van dit instrument
        kunnen dan ook geen conclusies worden verbonden.
      </b-alert>
      <h1 class="display-3 title">Kwetsbaarheidsanalyse personele veiligheid</h1>
      <h3 class="display-4 subtitle">Resultaten analyse functie {{ functieNaam }}</h3>
    </b-row>
    <b-row :class="{'export-class': forExport, 'html2pdf__page-break': forExport, 'effectRow': true}" v-for="(effectKey, index) in displayEffecten" :key="effectKey"  >
      <b-col>
        <p class="effectName"><em>{{ effecten[effectKey] }}</em></p>
        <b-row>
          <b-col cols="4" class="gauge-wrapper">
            <vue-svg-gauge
              :start-angle="-110"
              :end-angle="110"
              :value="totalScores[effectKey]"
              :separator-step="0"
              :min="0"
              :max="8"
              :inner-radius="60"
              :gauge-color="gaugeColor"
              :scale-interval="1">
            </vue-svg-gauge>
            <div class="gauge-text">
                {{ totalScoresCategories[effectKey] }}
            </div>
          </b-col>

          <b-col cols="8">
            <h3 class="negatief-positief-header">Versterkende factoren</h3>
            <b-row v-if="negatieveFactoren[effectKey].length > 0" class="reason">
              <div v-for="(reason, index) in negatieveFactoren[effectKey]" :key="`${effectKey}-${index}`">
                <b-icon-patch-plus-fill class="plus" v-if="reason[2] > 1.0"></b-icon-patch-plus-fill> 
                <b-icon-patch-minus-fill class="minus" v-else></b-icon-patch-minus-fill> 
                &nbsp;<b>{{ translateFactorString(reason[0]) }}:</b> {{ reason[1] }}
              </div>
            </b-row>
            <b-row v-else class="reason">
              <div> - </div>
            </b-row>

            <h3 class="negatief-positief-header">Mitigerende factoren</h3>
            <b-row class="reason" v-if="positieveFactoren[effectKey].length > 0" >
              <div v-for="(reason, index) in positieveFactoren[effectKey]" :key="`${effectKey}-${index}`">
                <b-icon-patch-plus-fill class="plus" v-if="reason[2] > 1.0"></b-icon-patch-plus-fill> 
                <b-icon-patch-minus-fill class="minus" v-else></b-icon-patch-minus-fill> 
                &nbsp;<b>{{ translateFactorString(reason[0]) }}:</b> {{ reason[1] }}
              </div>
            </b-row>
             <b-row v-else class="reason">
              <div> - </div>
            </b-row>
          </b-col>

            
          <!-- <b-col cols="4" v-else><em>Geen relevante factoren om te tonen.</em></b-col> -->
       
        </b-row>
        <hr v-if="(index < 3) && forExport">
      </b-col>
    </b-row>
  </b-container>

</template>

<script>
  import { VueSvgGauge } from 'vue-svg-gauge'
  
  export default {
    components: {
      VueSvgGauge,
    },
    props: {
      effecten: Object,
      displayEffecten: Array,
      totalScores: Object,
      totalScoresOpbouw: Object,
      totalScoresCategories: Object,
      forExport: Boolean,
      functieNaam: String,
    },
    data() {
      return {
        render: false,
        gaugeColor: [
          {offset: 0,  color: '#B3FF78'},
          {offset: 12.5,  color: '#B3FF78'},
          {offset: 42, color: '#F2EE78'},
          {offset: 70, color: '#FF8441'},
          {offset: 100, color: '#FF0F00'},
        ],
      }
    },
    mounted() {
      // Only go about rendering when actual data is passed to the object
      if (Object.keys(this.totalScores).length > 0) {
        this.render = true;
        this.negatieveFactoren = this.getNegatieveFactoren();
        this.positieveFactoren = this.getPositieveFactoren();
      }
    },  
    methods: {
      getNegatieveFactoren() {
        let negatieveFactorenAlleEffecten = {};
        for (const [effect, opbouw] of Object.entries(this.totalScoresOpbouw)) {
          let negatieveFactoren = []
          for (const factorScoreArray of opbouw) {
            // factorScoreArray = [indicator--factor, gekozen variabele, coefficient]
            if (factorScoreArray[2] > 1.0) {
              negatieveFactoren.push(factorScoreArray)
            }
          }
          negatieveFactoren.sort((a, b) => {return b[2] - a[2]});
          negatieveFactorenAlleEffecten[effect] = negatieveFactoren;
        }
        return negatieveFactorenAlleEffecten;
      },
      getPositieveFactoren() {
        let positieveFactorenAlleEffecten = {};
        for (const [effect, opbouw] of Object.entries(this.totalScoresOpbouw)) {
          let positieveFactoren = []
          for (const factorScoreArray of opbouw) {
            // factorScoreArray = [indicator--factor, gekozen variabele, coefficient]
            if (factorScoreArray[2] < 1.0) {
              positieveFactoren.push(factorScoreArray)
            }
          }
          positieveFactoren.sort((a, b) => {return a[2] - b[2]});
          positieveFactorenAlleEffecten[effect] = positieveFactoren;
        }
        return positieveFactorenAlleEffecten;
      },
      capitaliseString(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
      },
      translateFactorString(string) {
        return this.capitaliseString(string.replace("--", " | ").replace("-", " "));
      },
    },
  }
  
  </script>

